section {
  margin-bottom: 40px;
}

.toolbox, .subscription, .wallet {
  border: 1px solid #ddd;
  padding: 20px;
  border-radius: 8px;
  background-color: #f9f9f9;
}

.toolbox h2 {
  margin: 20px;
}

h1 {
  font-size: 24px;
  color: #333;
}

h2 {
  font-size: 18px;
  color: #555;
  margin: 10px 0;
}

.link {
  color: #007bff;
  text-decoration: none;
}

.link:hover {
  text-decoration: underline;
}

.text {
  font-size: 16px;
  color: #666;
}

.subscription-link {
  font-size: 14px;
  color: #444;
  word-break: break-all;
  margin: 20px;
}

.qr-code {
  width: 150px;
  height: 150px;
}

.item {
  margin: 20px;
}